let game = document.getElementById("game__canvas");
let root = game.getContext("2d");

let Records = {}

function main(){
  let gameSetting = {
    score : 0,
    grip : 20,
  };
  let Snake = {
    x: 220,
    y: 220,
    cells: [],
    startCells:1,
    size: 20,
    dx: 0,
    dy: 0,
  };
  let Apply = {
    size: 20,
    x : 120,
    y : 120
  };
  let nameEntered = prompt("Enter your name, please:",'');
  if(nameEntered === null || nameEntered === "") nameEntered = "Player";


  document.addEventListener('keydown',function(e){
    keyActive=e.target;
   if(e.keyCode === 37 || e.keyCode === 65) {
    //ArrowLeft
    Snake.dx = -Snake.size;
    Snake.dy =0;
    
   }
   if(e.keyCode === 38 || e.keyCode === 87) {
    //ArrowUp
    Snake.dx = 0;
    Snake.dy = -Snake.size;
   }
   if(e.keyCode === 39 || e.keyCode === 68) {
    //ArrowRight
    Snake.dx = Snake.size;
    Snake.dy = 0;
   }
   if(e.keyCode === 40 || e.keyCode === 83) {
    //ArrowDown
    Snake.dx = 0;
    Snake.dy = Snake.size;
   }
  },false);

  let scoreDocument = document.getElementById("score");
  scoreDocument.innerHTML = gameSetting.score;


  function randomApply(max=24){
    return Math.floor(Math.random() * max);
  }

  function sortObject(obj) {
    return Object.entries(obj)
    .sort(([,a],[,b]) => b-a)
    .reduce((r, [k, v]) => ({ ...r, [k]: v }), {});
  }

  function getListContent() {
    while (ul.firstChild) {
      ul.removeChild(ul.firstChild);
    }
    let fragment = new DocumentFragment();
    let i=0;
    let sortedMyObject = sortObject(Records);
    for(let key in sortedMyObject) {
      let li = document.createElement('li');
      li.append(` ${ i + 1 }. ${ key } : ${ sortedMyObject[key] } `);
      fragment.append(li);
      i++;
    }
    return fragment;
  }
  function gameOver(){

    if( Records[nameEntered] > 0) {
      let valueScore = Records[nameEntered];
      if(Records[nameEntered] < gameSetting.score) {
        Records[nameEntered] = gameSetting.score;
      } else{
        Records[nameEntered] = valueScore;
      }
    } else {
      Records[nameEntered] = gameSetting.score;
    }
    let listTop = [];
    ul.append(getListContent());
    main();
    clearInterval(interval);

  }


  function collision(){
    if( game.width < Snake.x + Snake.size||
      0 > Snake.x ||
      game.height  < Snake.y + Snake.size ||
      0 > Snake.y ){
        gameOver();
    }
    if(Snake.x === Apply.x && Snake.y === Apply.y){
      Apply.x=randomApply()*gameSetting.grip;
      Apply.y=randomApply()*gameSetting.grip;
      Snake.startCells++;
      scoreDocument.innerHTML = ++gameSetting.score;
    }
    for (let j = 1; j < Snake.startCells; j++) {
        if(Snake.cells[0].x === Snake.cells[j].x && Snake.cells[0].y === Snake.cells[j].y) {
            gameOver();
        }
    }
  }




  function drawApply(){
    root.beginPath();
    root.rect(Apply.x, Apply.y, Apply.size, Apply.size);
    root.fillStyle = "#f00";
    root.fill();
    root.closePath();

  }



  function drawSnake() {
    root.beginPath();
    root.fillStyle = "green";
    Snake.cells.unshift({x:Snake.x, y:Snake.y});
    root.rect(Snake.x, Snake.y, Snake.size - 1, Snake.size - 1);
    for (let i = 0; i < Snake.startCells; i++) {
      createRect(Snake.cells[i].x,Snake.cells[i].y,Snake.size - 1,Snake.size - 1); 
    }
    root.fill();
    root.closePath();
  }


  function createRect(x,y,width,height){
     root.rect(x, y, width, height);
  }


  function draw() {
    root.clearRect(0, 0, game.width, game.height);
    drawSnake();
    collision();
    drawApply();
    Snake.x+= Snake.dx;
    Snake.y+= Snake.dy;
  }

  let interval = setInterval(draw, 1000 / (10 + gameSetting.score * 2 ));
}

main();